import React, {ChangeEvent, useState} from 'react';
import style from "./Register.module.css";
import AuthHeader from "../Header/AuthHeader";
import {useFormik} from "formik";
import {NavLink} from "react-router-dom";
import closeIcon from "../../../../assets/icons/+.svg";
import {useNavigate} from "react-router-dom"

type ErrorsType = {
    email: string
    password: string
    confirmPassword : string
}
const Register = () => {
    const navigate = useNavigate()
    /**
     *        .. ,   
     */
    const [showNewPassModal, setShowNewPassModal] = useState<boolean>(false)

    /**
     *       ... ,   
     */
    const showModal2 = () => {
        setShowNewPassModal(true)
    }
    /**
     *    
     */
    const closeModal2 = () => {
        setShowNewPassModal(false)
    }
    /**
     *       
     */
    const [showRecoverModal, setShowRecoverModal] = useState<boolean>(false)

    /**
     *      
     */
    const showModal = () => {
        setShowRecoverModal(true)
    }
    /**
     *    
     */
    const closeModal = () => {
        setShowRecoverModal(false)
    }
    /**
     *       
     */
    const [showPassword, setShowPassword] = useState<boolean>(false);
    /**
     *      
     */
    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    };

    /**
     *      
     */
    const isValidEmail = (email: string) => {
        const emailRegex = /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
        return emailRegex.test(email);
    };

    const handleCheckboxChange = (event : ChangeEvent<HTMLInputElement>) => {
        const { checked } = event.target;
        formik.setFieldValue('isChecked', checked);
    };
    /**
     *   ,  , 
     */
    const formik = useFormik({
        initialValues: {
            email: '',
            password: '',
            confirmPassword : '',
            isChecked : false
        },
        onSubmit: values => {
            alert(JSON.stringify(values, null, 2));
            formik.resetForm()
        },
        validate: (values) => {
            const errors: any = {};

            if (!values.email) {
                errors.email = ",    e-mail";
            } else if (!isValidEmail(values.email)) {
                errors.email = ",   e-mail";
            }

            if (!values.password) {
                errors.password = ",  ";
            }
            if(!values.confirmPassword){
                errors.confirmPassword = ",  ";
            } else if (values.confirmPassword && values.password !== values.confirmPassword) {
                errors.confirmPassword = "  .   ";
            }
            if(!values.isChecked){
                errors.isChecked = "error"
            }
            return errors;
        },
    });
    const formik2 = useFormik({
        initialValues: {
            emailRecoverPassword: ''
        },
        onSubmit: values => {
            alert(JSON.stringify(values, null, 2));
            formik2.resetForm()
            setTimeout(()=> {
                closeModal()
                showModal2()
            })
        },
        validate: (values) => {
            const errors: any = {};

            if (!values.emailRecoverPassword) {
                errors.emailRecoverPassword = ",    e-mail";
            } else if (!isValidEmail(values.emailRecoverPassword)) {
                errors.emailRecoverPassword = ",   e-mail";
            }
            console.log(errors)
            return errors;
        }
    });
    const formik3 = useFormik({
        initialValues: {
            newPassword: '',
            confirmNewPass : ''
        },
        onSubmit: values => {
            alert(JSON.stringify(values, null, 2));
            formik2.resetForm()
            closeModal2()
            navigate("/login")
        },
        validate: (values) => {
            const errors: any = {};

            if (!values.newPassword) {
                errors.newPassword = ",  ";
            }
            if(!values.confirmNewPass){
                errors.confirmNewPass = ",  ";
            } else if (values.confirmNewPass && values.newPassword !== values.confirmNewPass) {
                errors.confirmNewPass = "  .   ";
            }
            console.log(errors)
            return errors;
        }
    });
    return (
        <div className={style.container}>
            <div className={ showNewPassModal ? style.recoverModalBg : style.hiddenRecoverModalBg} onClick={closeModal2}>
                <div className={style.recoverModal} style={{height : '320px'}} onClick={(e)=> e.stopPropagation()}>
                    <h3> </h3>
                    <form onSubmit={formik3.handleSubmit} className={style.form}>
                        {/**
                         *     display : none,     value   
                         **/
                        }
                        <input
                            id={"newPassword"}
                            type="password"
                            placeholder={"  "}
                            value={formik3.values.newPassword}
                            onChange={formik3.handleChange}
                            onBlur={formik3.handleBlur}

                            className={
                                formik3.touched.newPassword && formik3.errors.newPassword
                                    ? style.recoverErrorInput
                                    : style.recoverInput
                            }
                        />
                        {formik3.touched.newPassword && formik3.errors.newPassword && (
                            <div className={style.errorMessage}>
                                {formik3.errors.newPassword}
                            </div>
                        )}
                        <input
                            id={"confirmNewPass"}
                            type="password"
                            placeholder={"  "}
                            value={formik3.values.confirmNewPass}
                            onChange={formik3.handleChange}
                            onBlur={formik3.handleBlur}

                            className={
                                formik3.touched.confirmNewPass && formik3.errors.confirmNewPass
                                    ? style.recoverErrorInput
                                    : style.recoverInput
                            }
                        />
                        {formik3.touched.confirmNewPass && formik3.errors.confirmNewPass && (
                            <div className={style.errorMessage}>
                                {formik3.errors.confirmNewPass}
                            </div>
                        )}
                        <button type="submit" className={style.recoverBtn} style={{marginTop : "10px"}}></button>
                    </form>
                    <img src={closeIcon} alt={"close"} onClick={closeModal2} className={style.closeIcon}/>
                </div>
            </div>
            <div className={ showRecoverModal ? style.recoverModalBg : style.hiddenRecoverModalBg} onClick={closeModal}>
                <div className={style.recoverModal} onClick={(e)=> e.stopPropagation()}>
                    <h3> </h3>
                    <form onSubmit={formik2.handleSubmit} className={style.form}>
                        {/**
                         *     display : none,     value   
                         **/
                        }
                        <input
                            id={"emailRecoverPassword"}
                            type="email"
                            placeholder={" e-mail"}
                            value={formik2.values.emailRecoverPassword}
                            onChange={formik2.handleChange}
                            onBlur={formik2.handleBlur}

                            className={
                                formik2.touched.emailRecoverPassword && formik2.errors.emailRecoverPassword
                                    ? style.recoverErrorInput
                                    : style.recoverInput
                            }
                        />
                        {formik2.touched.emailRecoverPassword && formik2.errors.emailRecoverPassword && (
                            <div className={style.errorMessage}>
                                {formik2.errors.emailRecoverPassword}
                            </div>
                        )}
                        <button type="submit" className={style.recoverBtn} style={{marginTop : "10px"}}></button>
                    </form>
                    <img src={closeIcon} alt={"close"} onClick={closeModal} className={style.closeIcon}/>
                </div>
            </div>
            <AuthHeader/>
            <div className={style.registerContainer}>
                <div className={style.registerFormContainer}>
                    <h3></h3>
                    <form onSubmit={formik.handleSubmit} className={style.form}>
                        <input
                            type="email"
                            placeholder={" e-mail"}
                            {...formik.getFieldProps("email")}
                            className={
                                formik.touched.email && formik.errors.email
                                    ? style.errorInput
                                    : style.input
                            }
                        />
                        {formik.touched.email && formik.errors.email && (
                            <div className={style.errorMessage}>
                                {formik.errors.email}
                            </div>
                        )}
                        <div className={formik.touched.password && formik.errors.password
                            ? style.errorInput
                            : style.input}>
                            <input
                                style={{width : "95%"}}
                                type={showPassword ? "text" : "password"}
                                placeholder={" "}
                                {...formik.getFieldProps("password")}

                            />
                            <i className='bx bx-show' onClick={handleClickShowPassword}></i>
                            {/*<span onClick={handleClickShowPassword}><img src="../../../../assets/icons/showPassSvg.svg" /></span>*/}
                        </div>

                        {formik.touched.password && formik.errors.password && (
                            <div className={style.errorMessage}>
                                {formik.errors.password}
                            </div>
                        )}
                        <div className={formik.errors.password
                            ? style.errorInput
                            : style.input}>
                            <input
                                style={{width : "95%"}}
                                type={showPassword ? "text" : "password"}
                                placeholder={" "}
                                {...formik.getFieldProps("confirmPassword")}

                            />
                            <i className='bx bx-show' onClick={handleClickShowPassword}></i>
                            {/*<span onClick={handleClickShowPassword}><img src="../../../../assets/icons/showPassSvg.svg" /></span>*/}
                        </div>

                        {formik.touched.confirmPassword && formik.errors.confirmPassword && (
                            <div className={style.errorMessage}>
                                {formik.errors.confirmPassword}
                            </div>
                        )}
                        <div className={style.approval}>
                            <label htmlFor="" className={style.customCheckBox}>
                                <input checked={formik.values.isChecked}
                                       onChange={handleCheckboxChange}
                                       type="checkbox" className={style.hiddenInput}/>
                                <span className={style.checkBoxIcon}></span>
                            </label>
                            <NavLink to={"/login"} className={style.loginLink} style={{marginLeft : "10px"}}>    </NavLink>
                        </div>
                        <button type="submit" className={style.btn}></button>
                    </form>
                    <div className={style.footer}>
                        <NavLink to={"/login"} className={style.loginLink}> ? </NavLink>
                        <a href={"#recoverPassword"} className={style.recoverLink} onClick={showModal}> ?</a>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default Register;