import React, {useEffect, useState} from 'react';
import style from './Header.module.css';
import {NavLink, useLocation} from "react-router-dom";
import bg from "../../../assets/image/bg-image-1.svg"

function Header() {
    /**
          
     */
    const [isActiveMenu, setIsActiveMenu] = useState<boolean>(false);
    /**
     *         
     */
    const [isActiveNav, setIsActiveNav] = useState<boolean>(false);

    /**
     *            +  
     */
    const handleClickMenu = () => {
        setIsActiveMenu(!isActiveMenu)
        setIsActiveNav(!isActiveNav)
    }
    const handleClickLink = () => {
        setIsActiveMenu(!isActiveMenu)
        setIsActiveNav(!isActiveNav)
    }
    /**
     *          
     */
    useEffect(() => {
        if (isActiveNav) {
            document.documentElement.classList.add("no-scroll");
        } else {
            document.documentElement.classList.remove("no-scroll");
        }
    }, [isActiveNav]);

    /**
     *             // 
     */
    const location = useLocation();

    useEffect(() => {
        const section = document.getElementById(location.hash.slice(1,location.hash.length));
        if (section) {
            section.scrollIntoView({ behavior: "smooth" });
        }
    }, [location.hash]);

    return (

        <header className={style.home}>
            <div className={style.homeImg}>
                {/*<img src={bg} alt=""/>*/}
            </div>
            {
                /**
                 *     :        
                 */
                isActiveNav ?
                    <nav className={style.mobileNavFul}>
                        <div className={style.mobileNavHeader}>
                            <h3>WattAttack</h3>
                            <i className={`bx ${isActiveMenu ? "bx-x" : "bx-menu"}`} id={style.menuIcon}
                               onClick={handleClickMenu}></i>
                        </div>
                        <a href="#howStart" onClick={handleClickLink}> </a>
                        <a href="#routes" onClick={handleClickLink}></a>
                        <a href="#training" onClick={handleClickLink}></a>
                        <a href="#about" onClick={handleClickLink}> </a>
                        <a href="#help" onClick={handleClickLink}></a>
                        <NavLink to={"/login"}></NavLink>
                        <NavLink to={"/download"}></NavLink>

                    </nav>
                    :
                    <nav className={style.mobileNavPart}>
                        <a href="#howStart"> </a>
                        <NavLink to={"/login"}></NavLink>
                        <NavLink to={"/download"}></NavLink>
                        <i className={`bx ${isActiveMenu ? "bx-x" : "bx-menu"}`} id={style.menuIcon}
                           onClick={handleClickMenu}></i>
                    </nav>
            }
            {/**
            *    ,      
            */}
            <nav className={style.navbar} id={style.home}>
                <div className={style.wrapper}>
                    <a href="#howStart"> </a>
                    <a href="#routes"></a>
                    <a href="#training"></a>
                    <a href="#about"> </a>
                    <a href="#help"></a>
                    <NavLink to={"/login"}></NavLink>
                    <NavLink to={"/download"}></NavLink>
                </div>
            </nav>

            <div className={style.heading}>
                <h1>WattAttack</h1>
                <p>   </p>
                <button className={style.btn}>!</button>
            </div>

        </header>


    );
}

export default Header;