import React, {useState} from 'react';
import style from "./AuthHeader.module.css";
import {NavLink} from "react-router-dom";

const AuthHeader = () => {
    /**
          
     */
    const [isActiveMenu, setIsActiveMenu] = useState<boolean>(false);
    /**
     *         
     */
    const [isActiveNav, setIsActiveNav] = useState<boolean>(false);
    /**
     *          
     */
    const handleClickMenu = () => {
        setIsActiveMenu(!isActiveMenu)
        setIsActiveNav(!isActiveNav)
    }
    const handleClickLink = () => {
        setIsActiveMenu(!isActiveMenu)
        setIsActiveNav(!isActiveNav)
    }
    return (
        <div className={style.container}>
            {
                /**
                 *     :        
                 */
                isActiveNav ?
                    <nav className={style.mobileNavFul}>
                        <div className={style.mobileNavHeader}>
                            <h3>WattAttack</h3>
                            <i className={`bx ${isActiveMenu ? "bx-x" : "bx-menu"}`} id={style.menuIcon}
                               onClick={handleClickMenu}></i>
                        </div>
                        <NavLink to="/#howStart" onClick={handleClickLink}> </NavLink>
                        <NavLink to="/#routes" onClick={handleClickLink}></NavLink>
                        <NavLink to="/#training" onClick={handleClickLink}></NavLink>
                        <NavLink to="/#about" onClick={handleClickLink}> </NavLink>
                        <NavLink to="/#help" onClick={handleClickLink}></NavLink>
                        <NavLink to={"/login"}></NavLink>
                        <NavLink to={"/download"}></NavLink>

                    </nav>
                    :
                    <nav className={style.mobileNavPart}>
                        <NavLink to={"/#howStart"}> </NavLink>
                        <NavLink to={"/login"}></NavLink>
                        <NavLink to={"/download"}></NavLink>
                        <i className={`bx ${isActiveMenu ? "bx-x" : "bx-menu"}`} id={style.menuIcon}
                           onClick={handleClickMenu}></i>
                    </nav>
            }
            {/**
             *    ,      
             */}
            <nav className={style.navbar} id={style.home}>
                <div className={style.wrapper}>
                    <NavLink to="/#howStart" onClick={handleClickLink}> </NavLink>
                    <NavLink to="/#routes" onClick={handleClickLink}></NavLink>
                    <NavLink to="/#training" onClick={handleClickLink}></NavLink>
                    <NavLink to="/#about" onClick={handleClickLink}> </NavLink>
                    <NavLink to="/#help" onClick={handleClickLink}></NavLink>
                    <NavLink to={"/login"}></NavLink>
                    <NavLink to={"/download"}></NavLink>
                </div>
            </nav>

        </div>
    );
};


export default AuthHeader;