import React from 'react';
import style from "./Training.module.css"
import TrainsItem from "./TrainsItem/TrainsItem";

export type TrainingDataType = {
    num: string
    title: string
    subtitle: string
}
const Training = () => {
    const trainingData: Array<TrainingDataType> = [
        {
            num: "01",
            title: " ",
            subtitle: "   ,     "
        },
        {
            num: "02",
            title: " ",
            subtitle: "  ,   "
        },
        {
            num: "03",
            title: "  ZWO ",
            subtitle: "  ZWO (Training Peaks, Zwift)     (    ,   )"
        },
        {
            num: "04",
            title: "  ",
            subtitle: "   "
        },
    ]
    return (
        <div className={style.container}>
            <div className={style.header}>
                <p>
                    
                </p>
                <span>
                    WattAttack     
                </span>
            </div>

            <div className={style.wrapper}>

                    <div className={style.trainsContainer} id={"training"}>
                        <div className={style.wrapWrapper}>
                        {trainingData.length > 0 ? trainingData.map(t => {
                                return (
                                    <TrainsItem key={t.num} trainsData={t}/>
                                )
                            })
                            :
                            ""
                        }
                    {/*    <div className={style.footer}>*/}
                    {/*        <h3>*/}
                    {/*                 Garmin, Strava, Training Peaks  . ,*/}
                    {/*        </h3>*/}
                    {/*        <span>*/}
                    {/*   */}
                    {/*    </span>*/}
                    {/*    </div>*/}
                    </div>
                        <div className={style.footer}>
                            <h3>
                                  
                            </h3>
                            <h3 style={{marginBottom : "7px"}}>
                                  Garmin, Strava, Training Peaks  . ,
                            </h3>
                            <span>
                       
                        </span>
                        </div>
                    </div>

            </div>

        </div>
    );
};

export default Training;